.gradient {
    float: left;
    width: 100%;
    background: rgb(2,0,36);
    background: linear-gradient( rgb(218, 160, 109) 0%, rgb(220, 180, 150) 30%, #FEF1E0 100%);
}
.flexbox {
  float: left;
  width: 100%;
  background-color: #FEF1E0;
}
* {
  box-sizing: border-box;
}

.menu {
  float: left;
  width: 18%;
  text-align: center;
  background-color: Wheat;
  box-shadow: 5px 5px 5px 5px grey;
  margin: 10px 12px 12px 10px ;
}
.paper{
    background: #FEF1E0;
    text-align: center;
    padding: 200px;
}
.menu a {
  background-color: Wheat;
  padding: 8px;
  spacing: 0px;
  display: block;
  width: 100%;
  height: 100%;
  color: black;
}

.main {
  float: left;
  width: 58%;
  height: 100%;
  padding: 10px;
  background-color: #FEF1E0;
}

.right {
  background-color: Wheat;
  box-shadow: 8px 8px 8px 8px grey;
  float: right;
  width: 18%;
  height: 100%;
  padding: 8px;
  text-align: center;
  margin: 10px 12px 12px 10px ;
}
.mycard {
    background-color: #FEF1E0;
    width: 25%;
    float: left;
}

@media only screen and (max-width: 800px) {
  /* For mobile phones: */
    .menu, .main, .right, .mycard {
        width: 100%;
        margin: 10px 0px 10px 0px;}
    .showoff{
        font-size: 40px;}
    .AppBar {
        padding: 0px;
        spacing: 0px;
        }
}

